    showPanel() {
      this.panelInstance = this.$showPanel({
        component: "panel-1",
        cssClass: "panel-1"
      });
    },
    get_content(event, info) {
      if (info.is_portal) {
        window.open(info.content_url, "_blank");
        return;
      }

      this.project_detail_loaded = false;
      this.showPanel();

      console.log("Getting " + info.content_url);

      this.$http.get(info.content_url).then(
        response => {
          document.getElementById("panel-content").innerHTML = response.body;
          this.project_detail_loaded = true;
        },
        response => {
          console.log("Failed!");
        }
      );
    }
  }
